/* Modern fonts and icons */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css');

/* CSS Variables for Light/Dark Mode */
:root {
  /* Light Mode (Default) */
  --bg-primary: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
  --bg-secondary: rgba(255, 255, 255, 0.1);
  --bg-card: rgba(255, 255, 255, 0.1);
  --bg-glass: rgba(255, 255, 255, 0.1);
  --text-primary: #ffffff;
  --text-secondary: rgba(255, 255, 255, 0.8);
  --text-muted: rgba(255, 255, 255, 0.6);
  --border-color: rgba(255, 255, 255, 0.2);
  --shadow-light: rgba(0, 0, 0, 0.1);
  --shadow-medium: rgba(0, 0, 0, 0.2);
  --accent-gradient: linear-gradient(45deg, #667eea 0%, #764ba2 100%);
  --navbar-bg: rgba(255, 255, 255, 0.1);
  --input-bg: rgba(255, 255, 255, 0.1);
  --playlist-bg: rgba(255, 255, 255, 0.05);
}

/* Dark Mode */
.dark {
  --bg-primary: linear-gradient(135deg, #0f0f23 0%, #1a1a2e 100%);
  --bg-secondary: rgba(30, 30, 30, 0.8);
  --bg-card: rgba(20, 20, 20, 0.9);
  --bg-glass: rgba(15, 15, 25, 0.8);
  --text-primary: #ffffff;
  --text-secondary: rgba(255, 255, 255, 0.9);
  --text-muted: rgba(255, 255, 255, 0.7);
  --border-color: rgba(255, 255, 255, 0.1);
  --shadow-light: rgba(0, 0, 0, 0.3);
  --shadow-medium: rgba(0, 0, 0, 0.5);
  --accent-gradient: linear-gradient(45deg, #4facfe 0%, #00f2fe 100%);
  --navbar-bg: rgba(10, 10, 20, 0.9);
  --input-bg: rgba(30, 30, 30, 0.8);
  --playlist-bg: rgba(20, 20, 30, 0.6);
}

/* Basic CSS Reset and Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Inter', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--bg-primary);
  min-height: 100vh;
  color: var(--text-primary);
  transition: background 0.3s ease, color 0.3s ease;
}

/* Main Container */
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Glass morphism cards */
.glass-card {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 8px 32px var(--shadow-light);
  border: 1px solid var(--border-color);
  transition: all 0.3s ease;
}

.glass-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px var(--shadow-medium);
}

/* Button Styles */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 15px 30px;
  border: none;
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1rem;
  position: relative;
  overflow: hidden;
}

.btn-primary {
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4);
  color: white;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

/* Header Styles */
.header {
  text-align: center;
  margin-bottom: 40px;
}

.header h1 {
  font-size: clamp(2rem, 5vw, 3.5rem);
  margin-bottom: 10px;
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.header p {
  font-size: 1.2rem;
  opacity: 0.9;
  margin-bottom: 30px;
}

/* Form and Input Styles */
.input-group {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
}

.mood-input, .form-input {
  flex: 1;
  padding: 15px 20px;
  border: none;
  border-radius: 50px;
  font-size: 1rem;
  background: rgba(255, 255, 255, 0.9);
  color: #333;
  outline: none;
  transition: all 0.3s ease;
}

.mood-input:focus, .form-input:focus {
  background: rgba(255, 255, 255, 1);
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.3);
}

/* Quick Action Chips */
.quick-moods, .mood-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}

.mood-chip {
  padding: 8px 16px;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.9rem;
}

.mood-chip:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
}

/* Playlist and Track Styles */
.playlist-section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 30px;
}

.playlist-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  padding: 25px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.playlist-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
}

.playlist-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
}

.playlist-icon {
  width: 60px;
  height: 60px;
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: white;
}

.playlist-info h3 {
  font-size: 1.4rem;
  margin-bottom: 5px;
}

.playlist-info p {
  opacity: 0.8;
  font-size: 0.9rem;
}

/* Track Item Styles */
.track-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.track-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  transition: all 0.3s ease;
  margin-bottom: 12px;
}

.track-item:hover {
  background: rgba(255, 255, 255, 0.1);
}

.album-art {
  width: 50px;
  height: 50px;
  border-radius: 8px;
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.2rem;
}

.track-info {
  flex: 1;
  min-width: 0;
}

.track-name {
  font-weight: 600;
  margin-bottom: 4px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.track-artist {
  opacity: 0.8;
  font-size: 0.9rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.play-btn {
  padding: 8px 12px;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  border-radius: 20px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

.play-btn:hover {
  background: rgba(255, 255, 255, 0.3);
}

/* Loading and Error States */
.loading {
  text-align: center;
  padding: 60px 20px;
}

.spinner {
  display: inline-block;
  width: 50px;
  height: 50px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: #4ecdc4;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.error {
  text-align: center;
  padding: 40px 20px;
  background: rgba(255, 107, 107, 0.1);
  border-radius: 15px;
  border: 1px solid rgba(255, 107, 107, 0.3);
}

.setup-notice {
  background: rgba(255, 193, 7, 0.1);
  border: 1px solid rgba(255, 193, 7, 0.3);
  border-radius: 15px;
  padding: 20px;
  margin-bottom: 20px;
  text-align: center;
}

/* Responsive Design */
@media (max-width: 768px) {
  .container {
    padding: 10px;
  }

  .input-group {
    flex-direction: column;
    gap: 10px;
  }

  .btn {
    width: 100%;
  }

  .quick-moods, .mood-chips {
    justify-content: center;
  }

  .mood-chip {
    font-size: 0.8rem;
    padding: 6px 12px;
  }

  .playlist-section {
    grid-template-columns: 1fr;
  }

  .track-item {
    padding: 10px;
  }

  .album-art {
    width: 40px;
    height: 40px;
  }

  .header h1 {
    font-size: 2rem;
  }

  .header p {
    font-size: 1rem;
  }
}

/* Grid and Layout Utilities */
.grid {
  display: grid;
  gap: 1.5rem;
}

.grid-cols-1 { grid-template-columns: repeat(1, 1fr); }
.grid-cols-2 { grid-template-columns: repeat(2, 1fr); }
.grid-cols-3 { grid-template-columns: repeat(3, 1fr); }

@media (min-width: 768px) {
  .md\:grid-cols-2 { grid-template-columns: repeat(2, 1fr); }
  .md\:grid-cols-3 { grid-template-columns: repeat(3, 1fr); }
}

@media (min-width: 1024px) {
  .lg\:grid-cols-3 { grid-template-columns: repeat(3, 1fr); }
  .lg\:grid-cols-4 { grid-template-columns: repeat(4, 1fr); }
}

/* Flex Utilities */
.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.items-center {
  align-items: center;
}

.justify-between {
  justify-content: space-between;
}

.justify-center {
  justify-content: center;
}

.space-x-2 > * + * {
  margin-left: 0.5rem;
}

.space-x-4 > * + * {
  margin-left: 1rem;
}

.space-y-4 > * + * {
  margin-top: 1rem;
}

.space-y-6 > * + * {
  margin-top: 1.5rem;
}

/* Text and Typography */
.text-center {
  text-align: center;
}

.text-sm {
  font-size: 0.875rem;
}

.text-lg {
  font-size: 1.125rem;
}

.text-xl {
  font-size: 1.25rem;
}

.text-2xl {
  font-size: 1.5rem;
}

.text-3xl {
  font-size: 1.875rem;
}

.font-medium {
  font-weight: 500;
}

.font-semibold {
  font-weight: 600;
}

.font-bold {
  font-weight: 700;
}

.text-white {
  color: white;
}

.opacity-80 {
  opacity: 0.8;
}

.opacity-90 {
  opacity: 0.9;
}

/* Spacing Utilities */
.mb-2 { margin-bottom: 0.5rem; }
.mb-4 { margin-bottom: 1rem; }
.mb-6 { margin-bottom: 1.5rem; }
.mb-8 { margin-bottom: 2rem; }

.mt-4 { margin-top: 1rem; }
.mt-8 { margin-top: 2rem; }
.mt-12 { margin-top: 3rem; }

.p-4 { padding: 1rem; }
.p-6 { padding: 1.5rem; }
.p-8 { padding: 2rem; }

.px-4 { padding-left: 1rem; padding-right: 1rem; }
.px-6 { padding-left: 1.5rem; padding-right: 1.5rem; }
.py-2 { padding-top: 0.5rem; padding-bottom: 0.5rem; }
.py-3 { padding-top: 0.75rem; padding-bottom: 0.75rem; }
.py-8 { padding-top: 2rem; padding-bottom: 2rem; }

/* Border and Shadow */
.rounded { border-radius: 0.25rem; }
.rounded-lg { border-radius: 0.5rem; }
.rounded-xl { border-radius: 0.75rem; }

.shadow { box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1); }
.shadow-lg { box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1); }

/* Layout */
.min-h-screen {
  min-height: 100vh;
}

.w-full {
  width: 100%;
}

.max-w-md {
  max-width: 28rem;
}

.max-w-4xl {
  max-width: 56rem;
}

.max-w-6xl {
  max-width: 72rem;
}

.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

.hidden {
  display: none;
}

@media (min-width: 768px) {
  .md\:block { display: block; }
  .md\:flex { display: flex; }
}

/* Navigation Styles */
.navbar {
  background: white;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 50;
}

.nav-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.nav-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 4rem;
}

.nav-brand {
  display: flex;
  align-items: center;
  font-size: 1.25rem;
  font-weight: 700;
  color: #3b82f6;
  text-decoration: none;
}

.nav-menu {
  display: none;
  align-items: center;
  gap: 2rem;
}

@media (min-width: 768px) {
  .nav-menu {
    display: flex;
  }
}

.nav-link {
  color: #4b5563;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s;
}

.nav-link:hover {
  color: #3b82f6;
}

.nav-link.active {
  color: #3b82f6;
}

.mobile-nav {
  display: block;
}

@media (min-width: 768px) {
  .mobile-nav {
    display: none;
  }
}

/* Hero Section */
.hero {
  background: linear-gradient(135deg, #3b82f6 0%, #8b5cf6 100%);
  color: white;
  padding: 4rem 0;
  text-align: center;
}

.hero h1 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.hero p {
  font-size: 1.25rem;
  margin-bottom: 2rem;
  opacity: 0.9;
}

@media (max-width: 767px) {
  .hero h1 {
    font-size: 2rem;
  }
  
  .hero p {
    font-size: 1rem;
  }
}

/* Form Styles */
.form-group {
  margin-bottom: 1rem;
}

.form-label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: #374151;
}

.form-input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  font-size: 1rem;
  transition: border-color 0.2s;
}

.form-input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.form-textarea {
  resize: vertical;
  min-height: 100px;
}

/* Loading Animation */
.loading {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Responsive Design */
@media (max-width: 767px) {
  .container {
    padding: 0 0.5rem;
  }
  
  .grid-cols-1 {
    grid-template-columns: 1fr;
  }
  
  .text-3xl {
    font-size: 1.5rem;
  }
  
  .text-2xl {
    font-size: 1.25rem;
  }
}

/* Additional utility classes for common needs */
.line-clamp-1 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  line-clamp: 1;
}

.line-clamp-2 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  line-clamp: 2;
}

.transition-colors {
  transition: color 0.2s, background-color 0.2s;
}

.hover\:bg-gray-100:hover {
  background-color: #f3f4f6;
}

.hover\:text-blue-600:hover {
  color: #2563eb;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Theme Toggle Styles */
.theme-toggle {
  position: relative;
  background: var(--bg-glass);
  border: 1px solid var(--border-color);
  color: var(--text-primary);
  border-radius: 50%;
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  font-size: 18px;
  box-shadow: 0 4px 15px var(--shadow-light);
}

.theme-toggle:hover {
  transform: scale(1.05) rotate(15deg);
  box-shadow: 0 6px 20px var(--shadow-medium);
  background: var(--bg-secondary);
}

.theme-toggle:active {
  transform: scale(0.95);
}

.theme-toggle i {
  transition: all 0.3s ease;
}

/* Navbar theme toggle positioning */
.navbar .theme-toggle {
  margin-left: auto;
}

/* Dark mode specific adjustments */
.dark .glass-card {
  background: var(--bg-card);
}

.dark .navbar {
  background: var(--navbar-bg);
}

.dark input, .dark textarea, .dark select {
  background: var(--input-bg);
  color: var(--text-primary);
  border-color: var(--border-color);
}

.dark input::placeholder, 
.dark textarea::placeholder {
  color: var(--text-muted);
}

.dark .playlist-item {
  background: var(--playlist-bg);
  border-color: var(--border-color);
}

.dark .btn-outline {
  border-color: var(--border-color);
  color: var(--text-primary);
}

.dark .btn-outline:hover {
  background: var(--bg-secondary);
}

/* Clerk Components Dark Mode Styling */
.dark .cl-card,
.dark .cl-modal-content,
.dark .cl-socialButtons,
.dark .cl-formButtonPrimary,
.dark .cl-footerAction,
.dark .cl-internal-b3fm6y,
.dark .cl-modalContent {
  background: var(--bg-card) !important;
  border-color: var(--border-color) !important;
}

.dark .cl-headerTitle,
.dark .cl-headerSubtitle,
.dark .cl-modalCloseButton,
.dark .cl-formFieldLabel,
.dark .cl-footerActionText,
.dark .cl-footerActionLink,
.dark .cl-internal-1memmyy {
  color: var(--text-primary) !important;
}

.dark .cl-formFieldInput,
.dark .cl-formFieldInput:focus,
.dark .cl-formFieldInput:hover {
  background: var(--input-bg) !important;
  color: var(--text-primary) !important;
  border-color: var(--border-color) !important;
}

.dark .cl-formButtonPrimary {
  background: var(--accent-gradient) !important;
  border: none !important;
}

.dark .cl-socialButtonsIconButton {
  background: var(--bg-secondary) !important;
  border-color: var(--border-color) !important;
}

.dark .cl-dividerLine {
  background: var(--border-color) !important;
}

.dark .cl-alternativeMethods {
  background: var(--bg-card) !important;
}

/* Clerk Sign-in/Sign-up Page Background */
.dark .cl-rootBox {
  background: var(--bg-primary) !important;
}

/* Override any light theme remnants in Clerk components */
.dark .cl-card .cl-internal-1ta0mtu,
.dark .cl-card .cl-internal-b3fm6y {
  background: var(--bg-card) !important;
}

.dark .cl-modalBackdrop {
  background: rgba(0, 0, 0, 0.8) !important;
}
